te(0deg);-webkit-filter:blur(8px);filter:blur(8px);border-radius:5px}}@keyframes pulse{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg);-webkit-filter:blur(8px);filter:blur(8px);border-radius:5px}33%{-webkit-transform:rotate(-0.5deg) translate(1px, -1px) scale(1.01);transform:rotate(-0.5deg) translate(1px, -1px) scale(1.01);-webkit-filter:blur(10px);filter:blur(10px);border-radius:3px}67%{-webkit-transform:rotate(1deg) translate(-1px, -1px) scale(0.99);transform:rotate(1deg) translate(-1px, -1px) scale(0.99);-webkit-filter:blur(14px);filter:blur(14px);border-radius:7px}100%{-webkit-transform:rotate(0deg);transform:rotate(0deg);-webkit-filter:blur(8px);filter:blur(8px);border-radius:5px}}@media only screen and (max-width: 500px){.header{font-size:32px}.subheader{font-size:15px}.paragraph{font-size:12px}.header-1{font-size:20px}span{font-size:x-small}.glow{width:55vw;height:25vh}.arch{width:50vw;height:20vh}.parent{height:30vh;margin-top:2rem}.tick{height:1rem;width:1rem}.features{width:75%}}
