.question-card{width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:1em;margin-bottom:2rem;background:linear-gradient(180deg, #28292c 0%, rgba(39, 40, 43, 0.82) 100%);box-shadow:0px 13px 40px #00000030,0px 4px 4px #00000020;border-radius:16px;padding:1rem}.card-title{color:#663fd6;font-weight:bold;text-align:left}.distro-names{color:#d4d4d4}.question-card{overflow-y:auto}::-webkit-scrollbar{width:6px}::-webkit-scrollbar-track{border-radius:16px}::-webkit-scrollbar-thumb{background:#414141;border-radius:16px}@media only screen and (max-width: 600px){.distro-names{font-size:small}}
